<script>
  import {mapState, mapActions} from 'vuex'
  import Api from '@/constants/Api.js'
  export default {
    data() {
      return {
        currentPage: 1, // 当前页数
        total: 0, // 总条目数
        loading: false,
        strategyForm: { // 查询表单参数
          custom_flag: false
        },
        monitorOptions: [], // 监控项下拉数据
        ipOptions: [], // 被管对象下拉数据
        addForm: { // 添加、编辑表单
          moLocator: [],
          metric: '',
          judge_function_alarm_times: '',
          judge_function_operators: '',
          judge_function_threshold: ''
        },
        strategyVisible: false, // 控制添加、编辑弹窗显示/隐藏

        editForm: { // 添加、编辑表单
          moLocator: [],
          metric: '',
          judge_function_alarm_times: '',
          judge_function_operators: '',
          judge_function_threshold: ''
        },
        editVisible: false, // 控制添加、编辑弹窗显示/隐藏
        formRules: {
          moType: [
            { required: true, message: '请选择对象类型', trigger: 'blur' }
          ],
          metric: [
            { required: true, message: '请选择监控项', trigger: 'blur' }
          ],
          moLocator: [
            { required: true, message: '请选择被管对象', trigger: 'blur' }
          ],
          judge_function_name: [
            { required: true, message: '请选择表达式函数', trigger: 'blur' }
          ],
          judge_function_alarm_times: [
            { required: true, message: '请选择频次', trigger: 'blur' },
            { pattern: /^\+?[1-9][0-9]*$/, message: '数量必须为数字值' }
          ],
          judge_function_operators: [
            { required: true, message: '请选择关系符', trigger: 'blur' }
          ],
          judge_function_interval: [
            { required: true, message: '请选择间隔时间', trigger: 'blur' },
            { pattern: /^\+?[1-9][0-9]*$/, message: '数量必须为数字值' }
          ],
          judge_function_threshold: [
            { required: true, message: '请选择阀值', trigger: 'blur' },
            { pattern: /^\+?[1-9][0-9]*$/, message: '数量必须为数字值' }
          ],
          level_id: [
            { required: true, message: '请选择关系符', trigger: 'blur' }
          ],
          alarm_max: [
            { required: true, message: '请选择告警次数', trigger: 'blur' },
            { pattern: /^\+?[1-9][0-9]*$/, message: '数量必须为数字值' }
          ],
          cool_down: [
            { required: true, message: '请选择冷却时间', trigger: 'blur' },
            { pattern: /^\+?[1-9][0-9]*$/, message: '数量必须为数字值' }
          ],
          email: [
            { required: false, message: '请选择邮箱', trigger: 'change' },
            { pattern: /^[a-z\d]+(\.[a-z\d]+)*@([\da-z](-[\da-z])?)+(\.{1,2}[a-z]+)+$/, message: '邮箱格式必须正确' }
          ]
        }
      }
    },
    computed: {
      ...mapState({
        strategyInfo: state => state.alarm.strategyInfo,
        objectType: state => state.alarm.objectType, // 对象类型
        LevelLogData: state => state.alarm.LevelLogData, // 级别
        strategyFunc: state => state.alarm.strategyFunc, // 表达式函数
        relation: state => state.alarm.relation // 关系符
      }),
      colorTo() {
        if (!this.strategyForm.custom_flag) {
          return 'primary'
        } else {
          return 'info'
        }
      },
      colorBack() {
        if (this.strategyForm.custom_flag) {
          return 'primary'
        } else {
          return 'info'
        }
      }
    },
    watch: {
      'addForm.moType'(newVal) {
        const vm = this
        if (newVal) {
          if (this.strategyVisible) {
            this.addForm.moLocator = []
            this.addForm.metric = ''
          }
          let box = {
            'Host': Api.hostSearchIpList,
            'Galaxy': Api.alarmNodesConfigCombox,
            'Connector.': Api.alarmNodesConfigCombox,
            'Zookeeper': Api.alarmgetInstallZKCombox,
            'Redis': Api.alarmInstallRedisCombox
          }
          if (newVal !== 'Zipkin') {
            let url = box[newVal] ? box[newVal] : box.Galaxy
            vm.$axios.post(url, {'moType': newVal}).then((ipData) => {
              this.ipOptions = ipData.data
            }).catch(error => {
              vm.$message.error(error.data.message || error.message || error)
            })
          } else {
            this.addForm.moLocator = ['不选择']
          }
          vm.$axios.get(Api.dictListMotype + newVal, {}).then((monitorData) => {
            this.monitorOptions = monitorData.data
          }).catch(error => {
            vm.$message.error(error.data.message || error.message || error)
          })
        }
      },
      'editForm.moType'(newVal) {
        const vm = this
        if (newVal) {
          if (this.editVisible) {
            this.editForm.moLocator = ''
            this.editForm.metric = ''
          }
          let box = {
            'Host': Api.hostSearchIpList,
            'Galaxy': Api.alarmNodesConfigCombox,
            'Connector.': Api.alarmNodesConfigCombox,
            'Zookeeper': Api.alarmgetInstallZKCombox,
            'Redis': Api.alarmInstallRedisCombox
          }
          if (newVal !== 'Zipkin') {
            let url = box[newVal] ? box[newVal] : box.Galaxy
            vm.$axios.post(url, {'moType': newVal}).then((ipData) => {
              this.ipOptions = ipData.data
            }).catch(error => {
              vm.$message.error(error.data.message || error.message || error)
            })
          } else {
            this.editForm.moLocator = 'Zipkin不选择'
          }
          vm.$axios.get(Api.dictListMotype + newVal, {}).then((monitorData) => {
            this.monitorOptions = monitorData.data
          }).catch(error => {
            vm.$message.error(error.data.message || error.message || error)
          })
        }
      }
    },
    activated() {
      if (this.objectType.length <= 0) {
        const vm = this
        this.primaryAlarm({vm})
      }
      this.search()
    },
    methods: {
      ...mapActions('alarm', ['primaryAlarm', 'serachAlarm', 'addAlarm', 'deleteAlarm', 'updateAlarm']),
      // 翻页事件
      handleCurrentChange(val) {
        const vm = this
        this.currentPage = val
        this.serachAlarm({vm})
      },
      // 搜索
      search() {
        this.loading = true
        this.currentPage = 1
        let vm = this
        this.serachAlarm({vm})
      },
      // 添加和修改功能的弹出框的显隐
      openHandle(title, obj) {
        if (title === 'editForm') { // 修改
          this.editForm = JSON.parse(JSON.stringify(obj))
          if (this.editForm.moType && this.editForm.moType === 'Zipkin') {
            this.editForm.moLocator = 'Zipkin不选择'
          }
          this.$nextTick(() => {
            this.editVisible = true
          })
        } else { // 添加
          this.addForm = {
            moLocator: [],
            metric: '',
            judge_function_alarm_times: '',
            judge_function_operators: '',
            judge_function_threshold: ''
          }
          this.$nextTick(() => {
            this.strategyVisible = true
          })
        }
      },
      cancel(title) {
        if (title === 'editForm') {
          this.$refs.editForm.resetFields()
          this.editVisible = false
        } else {
          this.$refs.addForm.resetFields()
          this.strategyVisible = false
        }
      },
      // 确定
      okHandle(title) {
        let box = null
        if (title === 'addForm') {
          box = this.addForm
        } else {
          box = this.editForm
        }
        const vm = this
        let query = Object.assign({}, JSON.parse(JSON.stringify(box)), {'custom_flag': this.strategyForm.custom_flag})
        this.$refs[title].validate((valid) => {
          if (valid) {
            if (query.moType === 'Zipkin') {
              query.moLocator = null
            }
            query.judge_function = query.judge_function_name + '(' + query.judge_function_threshold + ',' + query.judge_function_operators + ',' + query.judge_function_alarm_times + ',' + query.judge_function_interval + ' sec)'
            delete query.judge_function_name
            delete query.judge_function_alarm_times
            delete query.judge_function_operators
            delete query.judge_function_threshold
            delete query.judge_function_interval
            this.loading = true
            if (title === 'addForm') {
              query.moLocator_list = query.moLocator
              delete query.moLocator
              this.addAlarm({vm, query})
              this.strategyVisible = false
            }
            if (title === 'editForm') {
              this.updateAlarm({vm, query})
              this.editVisible = false
            }
          } else {
            vm.$message({
              message: '带*的值不能为空！',
              type: 'warning'
            })
          }
        })
      },
      // 删除功能
      deleteItem(id) {
        const vm = this
        this.conmmonMethods.makePopBox(this, '删除该策略？').then(() => {
          vm.loading = true
          vm.deleteAlarm({vm, id})
        })
      },
      // 启动
      statusChange(props) {
        this.loading = true
        const vm = this
        let query = Object.assign({}, JSON.parse(JSON.stringify(props.row)))
        delete query.judge_function_name
        delete query.judge_function_alarm_times
        delete query.judge_function_operators
        delete query.judge_function_threshold
        delete query.judge_function_interval
        if (query.enable) {
          query.enable = true
          this.updateAlarm({vm, query})
        } else {
          query.enable = false
          this.updateAlarm({vm, query})
        }
      },
      verifyHandle(value) {
        let epl = {'epl': value.epl}
        if (value.epl) {
          this.$axios({
            method: 'post',
            url: Api.alarmStragegyVerify,
            data: epl
          }).then((res) => {
            if (res.code === '0000') {
              this.$message({
                type: 'success',
                message: '格式正确'
              })
            }
          })
        } else {
          this.$message({
            message: '不能为空！'
          })
        }
      },
      // 切换
      handover(status) {
        this.strategyForm.custom_flag = status
        this.currentPage = 1
        let vm = this
        vm.loading = true
        this.serachAlarm({vm})
      }
    }
  }
</script>

<style lang="scss">
  .alarm-content {
    .el-button {
      [class^="fa fa-"] {
        margin-right: 5px;
      }
    }
    .normal{
      color: #ffffff;
      background: #909399;
      border: 1px solid #909399
    }
    .el-switch__input:focus~.el-switch__core {
      outline: none;
    }
    .el-select__tags{
      width: 100%;
      span{
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
    }
  }
</style>



